body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  font-family: "Helvetica Neue", Arial, "Noto Sans", sans-serif;
}

html {
  scroll-behavior: smooth;
}

.masthead {
  height: 100vh;
  min-height: 500px;
  background-image: url(/portfolio/static/media/computer.358a28af.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.container__opacity {
  opacity: 0.8;
  scroll-behavior: smooth;
}

.font-image {
  font-weight: 900;
  color: white;
  font-size: 10vh;
}

.font-image-p {
  color: white;
}

.font-body {
  font-size: 8vh;
}

.contact {
  margin-top: 50px;
}

.footer__icon {
  width: 30px;
  margin: 15px;
}

.footer__icon--link {
  width: 35px;
  margin: 20px;
  opacity: 0.7;
}

.footer__icon--link:hover {
  opacity: 1;
}

.copyright {
  margin-top: 25px;
  font-size: 2vh;
}

.portfolio__image {
  width: 49%; 
}
